.switch {
    position: absolute;
    z-index: 5;
    bottom: 4.5%;
    align-items: center;
    display: flex;
    flex-wrap: nowrap;
    right: 0;
    margin-right: 10%;
    width: auto;
}
.switch__spans {
    display: flex;
    flex-wrap: nowrap;
    width: auto;
    padding-right: 14px;
    position: static;
}
.switch__btns {
    display: flex;
    flex-direction: row;
}
.left__switch {
    position: relative;
    border-radius: 50%;
    width: 37.6px;
    height: 37.6px;
    background: rgb(54 54 54);
    cursor: pointer;
    margin-right: 0.5rem;
    border-radius: 20px;
    border: 0;
    transform: rotate(180deg);
    justify-content: center;
    align-items: center;
    display: flex;
}
.right__switch {
    justify-content: center;
    align-items: center;
    display: flex;
    position: relative;
    border-radius: 50%;
    width: 37.6px;
    height: 37.6px;
    background: rgb(54 54 54);
    cursor: pointer;
    border: 0;
    border-radius: 20px;
}
.right__switch:hover {
    background: #ff5b00;
}
.left__switch:hover {
    background: #ff5b00;
}

@media screen and (max-width:800px){
    .switch {
        width: 98px;
        height: 9px;
        margin-right: 0px;
        left:0;
        right:0;
        margin: auto;
    }
    .switch__btns {
        display: none;
    }
}