.wrapper {
}

.Licenses__wrapper {
    margin: 0 auto;
    max-width: 1564px;
}
.Licenses {
    margin-top: 11.75vh;
    color: #cccccc;
    padding: 0 32px;
}
.title {
    font-size: 32px;
    margin-bottom: 5vh;
    font-weight: 700;
    color: #fff;
}
.text >ul>li{
    font-size: 20px;
}
.text {
    margin-bottom: 3vh;
}
.first {
    font-size: 32px;
    margin-bottom: 3vh;
}
.second {
    margin-bottom: 4vh;
}
.third {
    margin-bottom: 5.5vh;
}
.InfoXboxAndPC {
    font-size: 29px;
    margin-bottom: 13vh;
}
.InfoXboxAndPC__title {
    font-size: 29px;
    margin-bottom: 4vh;
}
.InfoXboxAndPC__btns {
    font-size: 29px;
    display: flex;
    flex-direction: column;
}
.InfoXboxAndPC__btns__btn_wrapper {
    border: 1px solid #a3a3a3;
    margin-bottom: -1px;
    padding-bottom: 8px;
    position: relative;
    cursor: pointer;
    
}
.InfoXboxAndPC__btns__btn_wrapper:first-of-type {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}
.InfoXboxAndPC__btns__btn_wrapper:last-of-type {
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}
.InfoXboxAndPC__btns__btn{
    padding-left: 75px;
    padding-right: 38px;
    cursor: pointer;
    padding: 30px 86px 20px 34px;
    position: relative;
    display: block;
}
.InfoPS {
    font-size: 29px;
    margin-bottom: 5vh;
}
.InfoPS__title {
    font-size: 29px;
    margin-bottom: 4vh;
}
.InfoPS__btns {
    font-size: 29px;   
    animation-delay: 0.5s 
}
h3 {
    font-size: 24px;
    font-weight: 500;
}
b {
    font-size: 16px;
}
.text__p {
    font-size: 16px;
    margin-top: 2vh;
    position: relative;
    line-height: 24px;
}
p {
    font-size: 16px;
    margin-top: 2.5vh;
    line-height: 27px;
}
.text__p:before {
    content: "";
    position: relative;
    left: 0px;
    top: 11px;
    background: #666666;
    width: 11px;
    height: 1.5px;
    display: inline-block;
    vertical-align: top;
    margin-right: 10px;
}
a {
    cursor: pointer;
    text-decoration: none;
    color: #f1f1f1;
    font-size: 16px;
}
a:hover {
    opacity: 0.65;
}
h3 {
    font-weight: 700;
    color: #fff;
    font-size: 28px;
}
h5 {
    font-weight: 500;
    line-height: 25px;
}