

.Basket__wrapper {
    margin: 0 auto;
    max-width: 1564px;
}
.Basket {
    padding: 0 32px;
    margin-top: 11.75vh;
    color: #cccccc;
}
.title {
    font-size: 32px;
    margin-bottom: 8vh;
    font-weight: 700;
    color: #fff;
}
.main__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}
.empty__basket {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.empty__basket__svg {
    margin-bottom: 3vh;
}
.empty__basket__svg svg {
    height: 150px;
    width: 150px;
    transform: scale(-1, 1)
}
.empty__basket__title {
    font-size: 36px;
    margin-bottom: 5vh;
    color: #bababa;
}
.empty__basket__text {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.empty__basket__text a {
    color: #fff;
    cursor: pointer;
    margin-right: 10px;
    font-size: 16px;
    font-weight: bold;
}
.empty__basket__text a:hover{
    opacity: 0.7;
}
.empty__basket__text_p {
    color: #696969;
    font-size: 16px;
}