.sliderLine {
    position: relative;
    display: flex;
    width: 400vw;
    height: 88.5vh;
    z-index: 5;
}
.sliderLine__img {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100vw;
    height: 88.5vh;
    max-width: 1564px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-x: 70%;
}