.navbar__wrapper {
    display: flex;
    align-items: center;
    background: transparent;
    position: sticky;
    z-index: 10;
    top: 0;
    right: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid rgba(255,255,255, 0.88);
    background-color: rgb(0, 0, 0, 0.92);
}
.navbar {
    margin-left: auto !important;
    margin-right: auto !important;
    max-width: 1564px;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.navbar__top {
    display: flex;
    padding: 0.8rem 0 0.8rem 32px;
    width: 100%;
}
.navbar__top__info {
    display: flex;
}
.navbar__top__info a {
    padding: 0 15px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-weight: 700;
    font-size: 14px;
    color: #fff;
    text-decoration: none;
}
.navbar__top__info a:hover {
    opacity: 0.6;
}
.navbar__main {
    z-index: 150;
    padding: 0 32px 0.3rem 32px;
    max-width: 1564px;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: row;
    position: relative;
}
.navbar__main:hover {
    opacity: 1;
}
.navbar__main__logo {
    cursor: pointer;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    text-decoration: none;
    flex: 0 1 calc(8%);
    margin-right: 8px;
}
.navbar__main__logo:hover {
    opacity: 1;
}
.navbar__main__logo__svg {
    margin-right: 12px;
}
.navbar__main__logo__svg:hover {
    opacity: 1;
}
.navbar__main__logo__text:hover {
    opacity: 1;
}
.navbar__main__logo__text {
    color: #fff;
    font-size: 23px;
    font-weight: bold;
    line-height: 1.65rem;
}
.navbar__main__catalog {
    cursor: pointer;
    display: flex;
    align-items: center;
    min-width: 150px;
    flex-shrink: 0;
    border: none;
    position: relative;
    height: 3rem;
    background: #ff5b00;
    border-radius: 12px;
    color: #fff;
    font-size: 19.2px;
    flex: 0 1 calc(9%);
    margin-right: 8px;
    margin-left: 8px;
}   
.navbar__main__catalog:hover {
    box-shadow: 0 0 0.3rem 0 rgb(215 22 88) inset, 0 0 0.3rem 0.1rem rgb(215 22 88);
}
.navbar__main__catalog__lines {
    transform: scale(0.8);
    bottom: 19%;
    right: 65%;
    position: absolute;
}
.navbar__main__catalog__cross {
    transform: scale(0.8);
    bottom: 27%;
    right: 68%;
    position: absolute;
    display: none;
}
.navbar__main__catalog__span {
    bottom: 26%;
    right: 17%;
    position: absolute;
    color: #fff;
    font-size: 1.2rem;
}
.navbar__main__search {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    border: 1px solid rgb(255, 255, 255, 0.4);
    border-radius: 10px;
    height: 100%;
    margin: 0;
    /* min-width: 800px; */
    min-width: auto;
    max-width: none;
    flex: 0 1 calc(74%);
    margin-right: 8px;
    margin-left: 8px;
}
@media screen  and (max-width:1000px) {
    .navbar__main__search {
        display: none;
    }
    .navbar__main__catalog {
        display: none;
    }
}
.navbar__main__search input {
    padding: 0.75rem 1rem 0.75rem 1rem;
    background: transparent;
    border: none;
    color: #fff;
    outline: none;
}
.navbar__main__search input::placeholder {
    color: #fff;
}
.navbar__main__search__buttons {
    padding: 0 16px 0 0;
    display: flex;
    flex-wrap: nowrap;
    height: 45.6px;
}
.first__button {
    cursor: pointer;
    flex-direction: row;
    align-items: center;
    display: flex;
    border: none;
    width: 72px;
    height: 48px;
    background: transparent;
    position: relative;
    margin-right: 1rem;
    justify-content: space-between;
    color: #fff;
    font-size: 14px;
}
.second__button {
    cursor: pointer;
    display: flex;
    align-items: center;
    height: 48px;
    fill: #fff;
    border: none;
    width: 19.2px;
    background: transparent;
}
.navbar__main__items {
    flex-direction: row;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    font-weight: bold;
    color: #fff;
    margin-left: 8px;
    flex: 0 1 calc(9%);
}
.navbar__main__item  {
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    padding: 0 0.4rem 0 0.4rem;
}
.navbar__main__item a  {
    align-items: center;
    cursor: pointer;
    display: flex;
    text-decoration: none;
    flex-direction: column;
    color: #fff;
}
.navbar__main__item a:hover {
    opacity: 0.6;
}
.navbar__main__item__adaptive {
    display: none;
    align-items: center;
    cursor: pointer;
    flex-direction: column;
    padding: 0 0.4rem 0 0.4rem;
}
.navbar__main__item__adaptive svg {
    width: 24px;
    height: 24px;
}
.navbar__main__item__adaptive:hover {
    opacity: 0.6;
}
.navbar__main__item__forever {
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    padding: 0 1.25rem 0 1.25rem;
}
.navbar__main__item__forever a {
    text-decoration: none;
    align-items: center;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    color: #fff;
}
.navbar__main__item__forever:hover {
    opacity: 0.6;
}

.enterAccount {
    background: transparent;
    border: none;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    align-items: center;
    display: flex;
    flex-direction: column;
}
.enterAccount:hover {
    opacity: 0.7;
}
.navbar__main__adaptive__wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.navbar__main__adaptive {
    display: none;
}
.navbar__main__hiddenCatalogBlock_wrapper {
    overflow: hidden;
    opacity: 0;
    position: absolute;
    background-color: rgb(34, 34, 34);
    width: calc(100% - 64px);
    top: 60px;
    border-radius: 8px;
}
.navbar__main__hiddenCatalogBlock {
    padding: 25px 0 40px 40px;
}
.header__menu__item__spans a{
    cursor: pointer;
    font-size: 15px;
    color: #fff;
}
.header__menu__item__spans a:not(:last-of-type){
    margin-bottom: 8px;
}
.header__menu__item__spans a:first-of-type{
    font-size: 16px;
    color: #fff;
    font-weight: bold;
    margin-bottom: 12px;
}
.header__menu__item__spans{
    display: flex;
    flex-direction: column;
    cursor: pointer;
    font-size: 15px;
    color: #fff;
}
.header__menu {
    display: flex;
    flex-direction: row;
    column-gap: 32px;
}
.header__menu__item {
    display: flex;
    flex-direction: row;
}
.header__menu__item__svg {
    width: 56px;
    height: 56px;
    margin-right: 20px;
}
.header__menu__item__svg svg {
    width: 56px;
    height: 56px;
}
@media screen  and (max-width:1000px) {
    .navbar__top {
        display: none;
    }
    .navbar__main__search {
        display: none;
    }
    .navbar__main__catalog {
        display: none;
    }
    h6 {
        display: none;
    }
    .navbar__main {
        justify-content: space-between;
        padding-top: 8px;
    }
    .navbar__main__item__adaptive {
        display: flex;
    }
    .navbar__main__adaptive {
        display: block;
        height: 24px;
        margin-right: 4px;
    }
    .navbar__main__logo__svg{
        margin-right: 4px;
        height: 45px;
        width: 55px;
    }
    .navbar__main__logo__svg svg{
        height: 45px;
        margin-right: 1px;
        width: 55px;
    }
    .navbar__main__logo__text {
        font-size: 20px;
        line-height: 18px;
    }
}


@media screen  and (max-width:600px) {
    .navbar__main__item__forever {
        display: none;
    }
}