@keyframes animate {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.InfoSubscription__wrapper {
    margin: 0 auto;
    max-width: 1564px;
    overflow-x: hidden;
}

.InfoSubscription {
    margin-top: 11.75vh;
    color: #cccccc;
    padding: 0 32px;
}
.title {
    font-size: 32px;
    margin-bottom: 5vh;
    font-weight: 700;
    color: #fff;
}
.text >ul>li{
    font-size: 20px;
}
.text {
    margin-bottom: 7.5vh;
}
.first {
    font-size: 32px;
    margin-bottom: 3vh;
}
.second {
    margin-bottom: 4vh;
}
.third {
    margin-bottom: 5.5vh;
}
.InfoXboxAndPC {
    font-size: 29px;
    margin-bottom: 13vh;
}
.InfoXboxAndPC__title {
    font-size: 29px;
    margin-bottom: 4vh;
}
.InfoXboxAndPC__btns {
    font-size: 29px;
    display: flex;
    flex-direction: column;
}
.InfoXboxAndPC__btns__btn_wrapper {
    border: 1px solid #a3a3a3;
    margin-bottom: -1px;
    padding-bottom: 8px;
    position: relative;
    cursor: pointer;
    
}
.InfoXboxAndPC__btns__btn_wrapper:first-of-type {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}
.InfoXboxAndPC__btns__btn_wrapper:last-of-type {
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}
.InfoXboxAndPC__btns__btn{
    padding-left: 75px;
    padding-right: 38px;
    cursor: pointer;
    padding: 30px 86px 20px 34px;
    position: relative;
    display: block;
}
.InfoPS {
    font-size: 29px;
    margin-bottom: 5vh;
}
.InfoPS__title {
    font-size: 29px;
    margin-bottom: 4vh;
}
.InfoPS__btns {
    font-size: 29px;   
    animation-delay: 0.5s 
}
h3 {
    font-size: 24px;
    font-weight: 500;
}
.InfoXboxAndPC__btns__btn__info {
    padding: 0px 33px 0px 33px;
}
.btn__info__text {
    padding-bottom: 13px;
}
.btn__info__text p {
    margin-bottom: 24px;
}
.InfoXboxAndPC__btns__btn__info__wrapper {
    display: block;
    animation-name: animate;
    animation-duration: 0.8s;
}
