.GameInCatalog__wrapper {
    margin: 0 auto;
    max-width: 1564px;
    justify-content: center;
}
.GameInCatalog {
    margin-top: 11.75vh;
    color: #fff;
    display: flex;
    flex-direction: column;
    max-width: 1500px;
    padding: 0 32px;
}
.GameInCatalog__top {
    display: flex;
    width: 100%;
    flex-direction: row;
}
.GameInCatalog__bottom {
    display: flex;
    flex-direction: column;
}
.left__block__wrapper {
    display: flex;
    flex: 0 1 calc(35%);
}
.left__block {
    width: 100%;
    height: 95%;
    padding-right: 41px;
}
.left__block img {
    border-radius: 13px;
    width: 100%;
    height: 100%;
}
.name {
    font-size: 32px;
    font-weight: bold;
    margin-bottom: 18px;
}
.right__block__wrapper {
    /* width: calc(100% - 594px - 41px); */
    flex: 0 1 calc(65%);
}
.detailInfo__wrapper {
    width: 100%;
    margin-bottom: 31px;
}
.detailInfo {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.line__items {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 40px;
}
.detailInfo__item {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.detailInfo__item__starSVG {
    margin-right: 5px;
}
.detailInfo__item__starCount {
    margin-right: 15px;
}
.reviews {
    cursor: pointer;
}
.reviews:hover {
    opacity: 0.65;
}
.detailInfo__item__favoriteBtn svg {
    fill:#a0a0a0;
    width: 20px;
    height: 20px;
}
.detailInfo__item__actionsBtn svg {
    fill:#a0a0a0;
    width: 20px;
    height: 20px;
}
.detailInfo__item__favoriteBtn {
    cursor: pointer;
    margin-right: 32px;
}
.detailInfo__item__actionsBtn {
    cursor: pointer;
}
.detailInfo__item__favoriteBtn:hover svg{
    fill: #fff;
}
.detailInfo__item__actionsBtn:hover svg{
    fill: #fff;
}
.item__svg {
    margin-right: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.item__svg svg {
    fill: #fff;
}
.detailInfo__main {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.detailInfo__main__right__wrapper {
    max-width: 356px;
}
.detailInfo__main__right__info__wrapper {
    background-color: #232323;
    border-radius: 10px;
}
.detailInfo__main__right__info {
    display: flex;
    flex-direction: column;
    padding: 22px 15px 22px 15px;
}

.item {
    display: flex;
    align-items: center;
    flex-direction: row;
}
.item:not(:last-of-type) {
    margin-bottom: 16px;
}
.item__text {
    font-size: 14px;
}
.allPrice {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 25px;
}
.newPrice {
    font-size: 24px;
    margin-right: 7px;
    font-weight: bold;
}
.oldPrice {
    position: relative;
    bottom: -1.5px;
    font-size: 16px;
    color: #999;
    text-decoration: line-through;
}
.basketBtn {
    width: 100%;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    background-color: #ff5b00;
    padding: 10.5px 0px 10.5px 0px;
}
.detailInfo__main__right__price__wrapper {
    background-color: #232323;
    border-radius: 12px;
    margin-bottom: 10px;
}
.detailInfo__main__right__price {
    padding: 24px 20px 24px 20px;
}
.detailInfo__main__left__wrapper {
    width: 52%;
}
.platform {
    display: flex;
    flex-direction: column;
}
.platform__title {
    display: flex;
    align-items: center;
    flex-direction: row;
    margin-bottom: 10px;
}
.platform__title__text {
    margin-right: 6px;
}
.platform__title__svg {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-right: 7px;
    height: 15px;
    width: 15px;
    border-radius: 50%;
}
.platform__title__svg:hover {
    background-color: #bd284c;
}
.platform__btnPS {
    border-radius: 10.5px;
    display: inline-block;
    padding: 8px 13px 8px 13px;
    border: 2px solid #a0a0a0;
    cursor: pointer;
    margin-bottom: 30px;
}
.platform__btnPS:not(:last-of-type) {
    margin-right: 10px;
}
.characteristic {
    display: flex;
    flex-direction: column;
}
.characteristic__title {
    margin-bottom: 15px;
}
.characteristic__release {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 10px;
}
.characteristic__genre {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 10px;
}
.characteristic__publisher {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 10px;
}
.characteristic__language {
    display: flex;    
    flex-direction: row;
    align-items: center;
    margin-bottom: 30px;
}
.characteristic__release span {
    font-size: 14px;
    color: #cccccc;
    margin-right: 4px;
}
.characteristic__genre span {
    font-size: 14px;
    color: #cccccc;
    margin-right: 4px;
}
.characteristic__publisher span {
    font-size: 14px;
    color: #cccccc;
    margin-right: 4px;
}
.characteristic__language span {
    font-size: 14px;
    color: #cccccc;
    margin-right: 4px;
}

h3 {
    font-size: 14px;
    color: #fff;
    font-weight: 500;
}
.characteristic__shortInfo {
    font-size: 14px;
    color: #cccccc;
    margin-bottom: 14px;
}
.characteristic__allInfo {
    font-size: 13px;
    cursor: pointer;
    text-decoration: underline;
    text-decoration-style: dotted;
    text-underline-offset: 0.15em;
}
.characteristic__allInfo:hover {
    opacity: 0.7;
}
.aboutGame {
    display: flex;
    flex-direction: column;
}

.tabs__items {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.tabs__items__description {
    cursor: pointer;
    position: relative;
}
.tabs__items__reviews {
    cursor: pointer;
    position: relative;
}
.tabs__items__howOrderGames {
    cursor: pointer;
    position: relative;
}
h4 {
    padding: 18px 5px 18px 5px;
    cursor: pointer;
    font-weight: 500;
    color: #cccccc;
}
.aboutGame__tabs {
    width: 100%;
}
.aboutGame__tabs__wrapper {
    height: auto;
    padding-top: 45px;
}
.tabs__line {
    width: 100%;
    border-bottom: 1px solid rgb(204, 204, 204, 0.5);
    margin-bottom: 45px;
}
.line__after {
    display: none;
    position: absolute;
    left: -1px;
    right: -1px;
    bottom: -1px;
    z-index: 1;
    height: 2px;
    -moz-border-radius-topleft: 3px;
    -webkit-border-top-left-radius: 3px;
    border-top-left-radius: 3px;
    -moz-border-radius-topright: 3px;
    -webkit-border-top-right-radius: 3px;
    border-top-right-radius: 3px;
    background: #ff5b00;
}
.aboutGame__content__description {
    display: block;
}
.aboutGame__content__reviews {
    display: none;
}
.aboutGame__content__howOrderGames {
    display: none;
}
.aboutGame__content__description {
    max-width: 1200px;
    color: #cccccc;
    line-height: 28px;
}

@media screen and (max-width:1200px) {
    .GameInCatalog__top {
        flex-direction: column;
        align-items: center;
    }
    .left__block__wrapper {
        margin-bottom: 40px;
    }
    .left__block {
        width: 454px;
        height: 454px;
        padding: 0;
    }
}

@media screen and (max-width:780px){
    .detailInfo__main {
        flex-direction: column;
    }
    .detailInfo__main__left__wrapper {
        width: 52%;
        margin-bottom: 60px;
    }
}
@media screen and (max-width:500px){
    
    .left__block {
        width: 100%;
        height: 350px;
    } 
}
@media screen and (max-width:420px){
    .tabs__items {
        overflow-x: scroll !important;
        overflow-y: hidden;
        width: 100%;
        padding: 0 0 6px 0;
    }
    .aboutGame__tabs__wrapper {
        margin-bottom: 28px;
    }
    /* .line__after {
        display: none;
        overflow: hidden;
        opacity: 0
    } */
    .tabs__line {
        display: none;
    } 
    .tabs__items__description {
        cursor: pointer;
        position: relative;
        min-width: 83.5px;
    }
    .tabs__items__reviews {
        cursor: pointer;
        position: relative;
        min-width: 83.5px;
    }
    .tabs__items__howOrderGames {
        cursor: pointer;
        position: relative;
        min-width: 199px;
    }
}
