.wrapper {
}

.InfoMail__wrapper {
    margin: 0 auto;
    max-width: 1564px;
}

.InfoMail {
    margin-top: 11.75vh;
    color: #cccccc;
    padding: 0 32px;
}
.title {
    font-size: 32px;
    margin-bottom: 5vh;
    font-weight: 700;
    color: #fff;
}
.text >ul>li{
    font-size: 20px;
}
.first {
    margin-bottom: 3vh;
}
.second {
    margin-bottom: 0.9vh;
}
.second span {
    cursor: pointer;
    font-size: 20px;
}
.second span:hover {
    opacity: 0.7;
}
.third {
    margin-bottom: 5.5vh;
}
.second:before {
    content: "";
    position: relative;
    left: 0px;
    top: 11px;
    background: #666666;
    width: 15px;
    height: 1.5px;
    display: inline-block;
    vertical-align: top;
    margin-right: 10px;
}
.third:before {
    content: "";
    position: relative;
    left: 0px;
    top: 11px;
    background: #666666;
    width: 15px;
    height: 1.5px;
    display: inline-block;
    vertical-align: top;
    margin-right: 10px;
}