
.InfoBooking__wrapper {
    margin: 0 auto;
    max-width: 1564px;
}

.InfoBooking {
    margin-top: 11.75vh;
    color: #cccccc;
    padding: 0 32px;
}
.title {
    font-size: 32px;
    margin-bottom: 5vh;
    font-weight: 700;
    color: #fff;
}
.text >ul>li{
    font-size: 20px;
}
.first {
    margin-bottom: 3vh;
}
.second {
    margin-bottom: 4vh;
    text-decoration: underline;
}
.third {
    margin-bottom: 5.5vh;
}
.first span {
    text-decoration: underline;
    color: blue;
    font-size: 20px;
    cursor: pointer;
}
.second span {
    font-size: 20px;
    font-weight: bold;
}