.container {
    margin-left: auto !important;
    margin-right: auto !important;
    max-width: 1564px;
}
.title {
    margin-top: 80px;
    padding: 0 32px 0 32px;
    margin-bottom: 2.2rem;
    display: flex;
    justify-content: space-between;
    width: 100%;
}
.title__left {
    display: flex;
    flex-wrap: nowrap;
    background: transparent;
    position: relative;
    align-items: center;
    cursor: pointer;
}
.title__left:hover {
    opacity: 0.75;
}
.title__left:hover  .rSwitch svg{
    fill: #ff5b00;
    opacity: 1;
}
.title__left__text {
    font-size: 1.5rem;
    background: transparent;
    color: #fff;
    border: none;
    margin-right: 1.3rem;
}
.title__left button {
    cursor: pointer;
    position: relative;
    display: flex;
    width: 1.5rem;
    height: 1.5rem;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    background: rgb(54 54 54);
    fill: #fff;
    border: none;
}
.title__right {
    display: flex;
    gap: 10px;
}
.title__right button{
    color: #fff;
    font-size: 1rem;
    height: 2.15rem;
    background: rgb(54 54 54);
    cursor: pointer;
    position: relative;
    border: none;
    border-radius: 7.5px;
    padding: 0 10px;
}
.title__right button:hover{
    opacity: 0.88;
}
.cards {
    display: grid;
    grid-gap: 1.5rem;
    grid-template-columns: repeat(4,1fr);
    padding: 0 32px 5rem 32px;
}
.card__wrapper {
    background: rgb(54 54 54);
    border: 1px solid rgb(110, 110, 110);
    border-radius: 7.5px;
    display: flex;
    color: #fff;
    font-weight: bold;
}
.card {
    padding: 1rem;
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
}
.card a {
    cursor: pointer;
}
.card__img {
    display: inline-block;
    width: 100%;
}
.card__img img {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    border-radius: 7.5px;
    height: 280px;
}
.card__info {
    margin-top: 0.75rem;
    position: relative;
    display: flex;
    flex-direction: column;
}
.card__info_price {
    font-size: 1.125rem;
    align-items: center;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    margin-bottom: 0.25rem;
}
.card__info_priceNew {
    margin-right: 0.3rem;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.card__info_priceOld {
    text-decoration: line-through;
    display: flex;
    flex-direction: row;
    align-items: center;
    opacity: 0.5;
}
.card__info_name {
    height: 4.125em;
    font-size: 0.9375rem;
}
.card__info__star {
    align-items: center;
    display: flex;
    flex-direction: row;
}
.card__info__star_nubmer {
    display: flex;
    align-items: center;
    flex-direction: row;
    margin-right: 0.65rem;
}
.number_star {
    font-size: 13px;
}
.includes {
    color: #69be15;
    font-size: 0.8125rem;
}
.star {
    margin-right: 0.4rem;
}
.card__btns {
    margin-top: 0.75rem;
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
    width: 48%;
}

.favourites {
    fill: rgb(110, 110, 110);
    background: transparent;
    border: none;
    cursor: pointer;
}
.favourites:hover {
    fill: #fff;
}
@media screen and (max-width:1564px){
    .cards {
        grid-template-columns: repeat(4, auto);
    }
}
@media screen and (max-width:1200px){
    .cards {
        grid-template-columns: repeat(3, auto);
    }
    .card__img img {
        height: 325px;
    }
    .card__btns {
        margin-top: 0.5rem;
        margin-bottom: 0.5rem;
    }
    .card__info_name {
        height: 3.4rem;
    }
}
@media screen and (max-width:1000px){
    .cards {
        grid-template-columns: repeat(2, auto);
    }
    .card__img img {
        height: 345px;
    }
    .card__btns {
        margin-top: 0.1rem;
        margin-bottom: 0;
    }
    .card__info_name {
        height: 2.8rem;
    }
}
@media screen and (max-width:770px){
    .title {
        flex-direction: row;
        flex-wrap: wrap;
    }
    .title__right {
        margin-top: 10px;
        width: 100%;
    }
    .title__right button {
        font-size: 14px;
    }
    .card__btns {
        width: 100%;
    }
}
@media screen and (max-width:600px){
    .cards {
        display: flex;
        overflow-x: scroll !important;
        overflow-y: hidden;
        padding-bottom: 55px;
    }
    .card__wrapper {
        height: 400px;
        min-width: 260px;
    }
    .card__img img {
        height: 220px;
    }
    .container {
        padding-bottom: 2rem;
    }
}

@media screen and (max-width:455px){
    .title__right {
        overflow-x: scroll !important;
        overflow-y: hidden;
        padding-bottom: 20px;
    }
    .title__right button {
        min-width: 145.7px;
    }
}