.btn {
    margin-top: 20px;
    display: flex;
    cursor: pointer;
    height: 2.5rem;
    width: 2.5rem;
    background-color: #ff5b00;
    border-radius: 50%;
    z-index: 9;
    justify-content: center;
    align-items: center;
    margin-left: 73%;
    position: sticky;
    bottom: 50px;
}
.btn svg {
    transform:rotate(270deg);
}