.btn {
    font-size: 0.875rem;
    font-family: 'Nunito', sans-serif;
    color: #fff;
    font-weight: bold;
    width: 6.408125rem;
    height: 2.1875rem;
    background: #ff5b00;
    border-radius: 7.5px;
    border: none;
    cursor: pointer;
    margin-right: 12px;
}
.btn:hover {
    box-shadow: 0 0 0.3rem 0 #ff5b00 inset, 0 0 0.3rem 0.1rem #ff5b00;
}