.Blog__wrapper {
    margin: 0 auto;
    max-width: 1564px;
}
.Blog {
    margin-top: 11.75vh;
    color: #fff;
    padding: 0 32px;
}
.title {
    font-size: 32px;
    margin-bottom: 5vh;
    font-weight: 700;
    color: #fff;
}
.main__wrapper {
    max-width: 1564px;
}
.main {
    max-width: 1500px;
    display: flex;
    flex-direction: row;
}
.left__block__wrapper{
    width: calc(100% - 64p - 270px);
}
.left__block {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-row-gap: 50px;
    grid-column-gap: 24px;
}
.right__block__wrapper {
    width: 270px;
    margin-left: 39px;
}
.right__block__nav {
    margin-bottom: 32px;
    display: flex;
    flex-direction: column;
}
.right__block__nav__all__wrapper {
    display: flex;
    flex-direction: row;
    cursor: pointer;
    margin-bottom: 10px;
}
.right__block__nav__all__wrapper:hover .right__block__nav__all {
    color: #adadad;
}
.right__block__nav__all {
    background-color: #343434;
    border-radius: 10px;
    padding: 5px 5px 5px 10px;
    display: flex;
    flex-direction: row;
}
.right__block__nav__all__text {
    margin-right: 10px;
}
.right__block__nav__all__number {
    padding-right: 5px;
}
.right__block__nav__review {
    display: flex;
    flex-direction: row;
    margin-left: 20px;
    margin-bottom: 10px;
    cursor: pointer;
}
.right__block__nav__review:hover {
    color: #adadad;
}
.right__block__nav__review__text {
    margin-right: 10px;
}
.right__block__nav__review__number {
}
.right__block__nav__preview {
    display: flex;
    flex-direction: row;
    margin-left: 20px;
    cursor: pointer;
}
.right__block__nav__preview:hover {
    color: #adadad;
}
.right__block__nav__preview__text {
    margin-right: 10px;

}
.right__block__nav__preview__number {

}
.right__block__btn__xbox {
    display: inline-block;
    padding: 5px 15px 5px 15px;
    background-color: transparent;
    border-radius: 10px;
    border: 1px solid #5c5c5c;
}
.right__block__btn__xbox__wrapper {
    cursor: pointer;
    margin-bottom: 30px;
}
.right__block__btn__xbox__wrapper:hover .right__block__btn__xbox{
    background-color: #2c2c2c;
}
.right__block__btn__subscribe__wrapper {
    width: 100%;
    border: 1px solid #5c5c5c;
    border-radius: 12px;
}
.right__block__btn__subscribe {
    display: flex;
    flex-direction: column;
    padding: 15px 20px 24px 20px;
}
.right__block__btn__subscribe__text {
    margin-bottom: 18px;
    font-weight: bold;
}
.right__block__btn__subscribe__btn {
    display: flex;
    padding: 10px 0 10px 0;
    justify-content: center;
    align-items: center;
    background-color: #ff5b00;
    border-radius: 10px;
    width: 100%;
    font-weight: bold;
    cursor: pointer;
}
.article {
    max-width: 381px;
}
.article a {
    height: 75%;
    display: inline-block;
}
.article:hover .article__text__title{
    opacity: 0.72;
}
.article__img {
    width: 100%;
    height: 100%;
}
.article__img img {
    width: 100%;
    border-radius: 10px;
    height: 100%;
}
.article__text__wrapper {
    padding: 13px 15px 0;
}
.article__text {
    display: flex;
    flex-direction: column;
}
.article__text__title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
    cursor: pointer;
}
.article__text__typeAndDate {
    font-size: 14px;
    color: #cccccc;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.article__text__typeAndDate span {
    color: #424242;
}
.article__text__typeAndDate__type {
    font-size: 14px;
    margin-right: 8px;
}
.article__text__typeAndDate__date {
    font-size: 14px;
    margin-left: 8px;
}
@media screen and (max-width:1200px){
    .left__block {
        grid-template-columns: repeat(2, auto);
        gap: 24px;
    }
    .left__block__wrapper{
        width: 100%;
    }
    .article {
        max-width: none;
        margin-bottom: 20px;
    }
}
@media screen and (max-width:1000px){
    .right__block__wrapper {
        display: none;
    }
}
@media screen and (max-width:768px){
    .left__block {
        grid-template-columns: repeat(1, auto);
        gap: 24px;
        grid-row-gap: 24px;
    }
    .article {
        max-width: none;
        margin: 0;
    }
}
@media screen and (max-width:600px){
    .article {
        max-width: none;
        margin-bottom: 12px;
    }
    .article__text__title {
        max-height: 48px;
        overflow-y: hidden;
    }
}
@media screen and (max-width:500px){
    .article {
        max-width: none;
        margin-bottom: 16px;
    }
}
@media screen and (max-width:480px){
    .article {
        max-width: none;
        margin-bottom: 20px;
    }
}