body {
    font-size: 16px;
}
body.modal {
    height: 100vh;
    overflow-y: hidden;
}
.ModalCatalog {
    position: fixed;
    height: 100vh;
    width: 100vw;
    display: none;
    background: rgb(0, 0, 0, 0.75);
    z-index: 20;
    left: 0;
    top: 0;
}
.ModalCatalog.active {
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.ModalCatalog__wrapper {
    cursor: auto;
    background: #0f0f0f;;
    border-radius: 8px;
    min-width: 250px;
    z-index: 20;
    height: 100%;
    position: absolute;
    left: 0;
}
.Catalog {
    padding: 30px 30px 0 30px;
}
.Catalog__menu__item__GameStore {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}
.Catalog__menu__item__GameStore__logo {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.Catalog__menu__item__logo__svg {
    margin-right: 10px;
}
.Catalog__menu__item__close svg {
    fill: #fff;
}
.Catalog__menu__item__title {
    font-size: 24px;
    line-height: 24px;
    font-weight: bold;
}
.Catalog__menu__item__account {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 20px;
}
.Catalog__menu__item__account__logo{
    margin-right: 12px;
}
.Catalog__menu__item__account__logo svg {
    fill: #fff;
}
.Catalog__menu__item__separator {
    height: 1px;
    border-bottom: 1px solid rgb(121, 121, 121);
}
.Catalog__menu__item__spans {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
}
.Catalog__menu__item__Catalog h5 {
    color: #fff;
    font-size: 18px;
    cursor: pointer;
}
.Catalog__menu__item__Catalog {
    margin-bottom: 12px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.Catalog__menu__item__Catalog__svg {
    width: 18px;
    height: 20px;
}
.Catalog__menu__item__Catalog__svg svg{
    width: 18px;
    height: 20px;
}
.Catalog__menu__item__spans a:not(:last-of-type){
    margin-bottom: 12px;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
}
.Catalog__menu__item__spans a:last-of-type{
    margin-bottom: 24px;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
}
.Catalog__menu__item__spans__navbarItem {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 18px;
}
.Catalog__menu__item__spans__navbarItem h5 {
    font-size: 18px;
}
.Catalog__menu__item__spans__navbarItem__logo {
    margin-right: 16px;
    height: 20px;
    width: 20px;
}
.Catalog__menu__item__spans__navbarItem__logo svg {
    height: 20px;
    width: 20px;
}
.Catalog__menu__item__Email {
    margin-top: 16px;
    color: #d4d4d4;
    font-size: 16px;
    margin-bottom: 20px;
}
.Catalog__menu__item__network {
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: space-between;
}
.Catalog__menu__item__network__link {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(95, 95, 95);
    width: 40px;
    height: 40px;
    border-radius: 8px;
    width: 70px;
    cursor: pointer;
}
.Catalog__menu__item__network__link svg{
    width: 24px;
    height: 25px;
}
