.infoSlider {
    position: absolute;
    left: 10.75%;
    top: 30%;
    color: #fff;
    z-index: 6;
    max-width: 788px;
}
.title {
    font-size: 34.4px;
    font-weight: bold;
    margin-bottom: 12px;
}
.release {
    font-size: 16px;
    margin-bottom: 18px;
}
.text {
    max-width: 41.9vw;
    margin-bottom: 28px;
}

@media screen and (max-width:1200px){
    .text {
        max-width: 80vw;
        margin-bottom: 24px;
        line-height: 18px;
    }
}
@media screen and (max-width:800px){
    .infoSlider {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        /* padding-left: 32px; */
        /* padding-right: 32px; */
        left:0;
        right:0;
        margin: auto;
    }
    .text {
        max-height: 35px;
        overflow-y: hidden;
    }
    .title {
        font-size: 30px;
    }
}