.swipperSpans {
    display: flex;
    width: auto;
    padding-right: 14px;
    position: static;
}

.span {
    cursor: pointer;
    width: 9px;
    height: 9px;
    border-radius: 50%;
    background: #a70199;
    position: relative;
}
.span:not(:last-of-type) {
    margin-right: 16px;
}