body {
    font-size: 16px;
}
body.modal {
    height: 100vh;
    overflow-y: hidden;
}
.ModalAcc {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgb(0, 0, 0, 0.75);
    z-index: 20;
}
.ModalAcc.active {
    display: flex;
    justify-content: center;
    align-items: center;
}
.myModalContent__wrapper {
    cursor: auto;
    padding: 8px;
    background: #0f0f0f;;
    border-radius: 8px;
    min-width: 250px;
    z-index: 20;
}
.myModalContent {
    max-width: 458px;
}
.close {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 32px;
    height: 32px;
    margin-left: auto;
}
.close svg {
    width: 16px;
    height: 16px;
    fill: rgb(102, 102, 102, 0.9);
}
.close:hover.close svg{
    fill: #9c9c9c;
}
.title {
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 20px;
    padding: 0 20px 5px 20px;
}
.form__footer__license_block {
    cursor: pointer;
}
.form__modal {
    padding: 0 20px 20px 20px;
}

.USER__LOGIN {
    margin-bottom: 15px;
}
.USER__PASSWORD {
    margin-bottom: 10px;
}
.USER__LOGIN__text {
    margin-bottom: 6.5px;
    font-size: 14px;
    color: #cccccc;
}
.USER__PASSWORD__text {
    margin-bottom: 6.5px;
    font-size: 14px;
    color: #cccccc;
}
input {
    width: 100%;
}
.USER__LOGIN__input input {
    padding: 10px 10px 10px 10px;
    border: 1px solid #808080;
    border-radius: 8px;
    background-color: transparent;
    color: #cccccc;
}
.USER__LOGIN__input input:focus{
    outline: 0;
    outline-offset: 0;
    background-color: #252525;
    border-color:#929292;
}
.USER__PASSWORD__input {
    display: flex;
    flex-direction: row;
    border: 1px solid #808080;
    border-radius: 8px;
    position: relative;
    display: flex;
    align-items: center;
}
.USER__PASSWORD__input input:focus {
    outline: 0;
    outline-offset: 0;
    background-color: #252525;
    border-color:#929292;
}
.USER__PASSWORD__input input {
    color: #cccccc;
    padding: 10px 30px 10px 10px;
    border: none;
    border-radius: 8px;
    background-color: transparent;
}
.USER__PASSWORD__input__svg__wrapper {
    cursor: pointer;
    position: absolute;
    right: 0;
    padding-right: 15px;
}
.USER__PASSWORD__input__svg__open svg {
    fill: #cccccc;
}
.USER__PASSWORD__input__svg__close svg {
    fill: #cccccc;
}
.saveUser__span {
    color: #cccccc;
    font-size: 14px;
}
.item a {
    font-size: 14px;
}
button span {
    font-size: 16px;
    font-weight: bold;
    color: #ffffff;
}
.button__enter {
    background-color: #ff5b00;
    border: none;
    border-radius: 8px;
    width: 100%;
    padding: 12px 0px 12px 0px;
    cursor: pointer;
    margin-bottom: 15px;
}
.button__registration {
    width: 100%;
    border: none;
    border-radius: 8px;
    background-color: #202020;
    padding: 12px 0px 12px 0px;
    cursor: pointer;
    margin-bottom: 15px;

}
.form__footer__actions__wrapper {
    margin-bottom: 20px;
}
.form__footer__action {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.form__footer__license_block {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}
.form__footer__license_block span {
    margin-right: 5px;
    color: #cccccc;
    font-size: 12px;
}
.form__footer__license_block  a {
    font-size: 12px;
}
.saveUser {
    display: flex;
    flex-direction: row;
    cursor: pointer;
    align-items: center;
}
.saveUser:hover .saveUser__span, .saveUser__square{
    color: #ffffff;
}
.saveUser__square {
    display: flex;
    width: 21px;
    height: 21px;
    border-radius: 3px;
    margin-right: 10px;
    border: 1px solid #929292;
    align-items: center;
    justify-content: center;
}
.saveUser__square__svg {
    fill: #ffffff;
    width: 14px;
    height: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
}