.container {
    background: #262626;
    margin: 0 auto;
    width: 100vw;
}
.sectionList {
    padding: 40px 32px 80px 32px;
    margin-left: auto !important;
    margin-right: auto !important;
    max-width: 1500px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    gap: 20px;
}

.btn__wrapper {
    height: 5.3rem;
    cursor: pointer;
    background: rgb(54 54 54);
    border-radius: 10px;
    border: 1px solid gray;
    align-items: center;
    display: flex;
    flex-direction: row;
    height: 91px;
}
.btn__wrapper:hover {
    background: rgb(88, 88, 88);
}
.btn {
    cursor: pointer;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 18px 28px 18px 24px;
    border: none;
    background: transparent;
}
.btn__svg__wrapper {
    background-color: #262626;
    border-radius: 12px;
}
.btn__svg {
    margin-right: 10px;
    width: 55px;
    height: 55px;
}
.btn__svg svg{
    width: 55px;
    height: 55px;
}

.btn__text {
    font-size: 1rem;
    color: #fff;
}
.textUnderSvg {
    color: #fff;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    font-size: 13.333px;
    max-width: 97%;
    margin-top: -5px;
}
.Steam__btn__svg__wrapper {
    margin-right: 10px;
    width: 55px;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.Steam__btn__svg {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #292929;
    border-radius: 12px;
    width: 36px;
    height: 36px;
}
.Steam__btn__svg svg {
    width: 24px;
    height: 24px;
}
@media screen and (max-width:600px) {
    .sectionList {
        flex-direction: column;
    }
}