

.FavoriteGames__wrapper {
    margin: 0 auto;
    max-width: 1564px;
    min-height: 200px;
}
.FavoriteGames {
    padding: 0 32px;
    margin-top: 11.75vh;
    color: #fff;
}
.title {
    font-size: 32px;
    margin-bottom: 5vh;
    font-weight: 700;
    color: #fff;
}
.main__wrapper {
    max-width: 1564px;
    margin-bottom: 5vh;
}
.main {
    max-width: 1500px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    min-height: 45vh;
}
.left__block__wrapper {
    width: 270px;
    margin-right: 39px;
}
.btn__subscribe__wrapper {
    cursor: pointer;
    padding: 4px 8px 4px 8px;
    margin-bottom: 5px;
}
.btn__subscribe__wrapper:hover {
    color: #b3b3b3;
}
.btn__favorite__wrapper {
    cursor: pointer;
    display: inline-block;
}
.btn__favorite__wrapper:hover {
    color: #b3b3b3;
}
.btn__favorite {
    background-color: #343434;
    border-radius: 10px;
    padding: 4px 8px 4px 8px;
}
.right__block__wrapper {
    width: calc(100% - 270px);
}
.noteGame {
    color: green;
}
