@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Nunito', sans-serif;
    font-size: 16px;
    scroll-behavior: smooth;
}
#root {
    background-color: #121212;
}
body {
    font-size: 16px;
}
body.modal {
    height: 100vh;
    overflow-y: hidden;
}