.thourthScreen {
    border-bottom: 1px solid rgba(158, 158, 158, 0.4);
    background: #262626;
    overflow-x: hidden;
    position: relative;
}
.container {
    padding-top: 5rem;
    padding-bottom: 5rem;
    position: relative;
    margin-left: auto !important;
    margin-right: auto !important;
    max-width: 1564px;
    width: 100%;
}
.title {
    padding: 0px 32px;
    margin-bottom: 2.2rem;
    display: flex;
    width: 100%;
}
.title__btn {
    display: flex;
    background: transparent;
    position: relative;
    align-items: center;
    cursor: pointer;
}
.title__btn:hover {
    opacity: 0.88;
}
.title__btn:hover .title__btn_svg svg {
    fill: rgb(215 22 88);
}
.title__btn span {
    font-size: 1.5rem;
    background: transparent;
    color: #fff;
    border: none;
    font-weight: bold;
    margin-right: 1.3rem;
}
.title__btn_svg {
    position: relative;
    display: flex;
    width: 1.5rem;
    height: 1.5rem;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    background: rgb(54 54 54);
    fill: #fff;
}
.main {
    margin-bottom: 2.2rem;
    display: flex;
    width: 100%;
}
.cardsVK {
    padding: 0px 32px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1.5rem;
}
.card__wrapper {
    cursor: pointer;
    background: rgb(54 54 54);
    border: 1px solid rgb(110, 110, 110);
    border-radius: 7.5px;
    display: flex;
    color: #fff;
    font-weight: bold;
    max-height: 33.2425rem;
}
.card__wrapper:hover .card__text{
    opacity: 0.75;
}
.card {
    width: 100%;
    padding: 20px;
    position: relative;
    display: flex;
    flex-direction: column;
}
.card__logoAndDate {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    margin-bottom: 0.5rem;
    font-size: 0.8125rem;
    color: #999999;
}
.logo {
    height: 1rem;
    margin-right: 0.75rem;
}
.card__text {
    max-height: 4.875rem;
    max-width: 19.6875rem;
    overflow: hidden;
    line-height: 1.55rem;
    width: 100%;
}
.card__img img{
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 100%;
    height: 180px;
    border-radius: 7.5px;
}
.checkHeightForBtnScrollToTop {
    height: 1px;
    width: 50vw;
    background: transparent;
}
@media screen and (max-width:1564px){
    .cardsVK {
        grid-template-columns: repeat(4, auto);
    }
}
@media screen and (max-width:1200px){
    .cardsVK {
        grid-template-columns: repeat(3, auto);
    }
}
@media screen and (max-width:1000px){
    .cardsVK {
        grid-template-columns: repeat(2, auto);
    }
}
@media screen and (max-width:600px){
    .container {
        padding-top: 5rem;
        padding-bottom: 0;
    }
    .cardsVK {
        display: flex;
        overflow-x: scroll !important;
        overflow-y: hidden;
        padding-bottom: 20px;
    }
    .card__wrapper {
        height: 400px;
        min-width: 260px;
    }
    .card__img img {
        height: 220px;
    }
}