.Registration__wrapper {
    margin: 0 auto;
    max-width: 1564px;
}
.Registration {
    margin-top: 10vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.title {
    font-size: 32px;
    margin-bottom: 5vh;
    font-weight: 700;
    color: #fff;
    display: flex;
    justify-content: center;
}
.Registration__form {
    display: flex;
    flex-direction: column;
}
.form__registration {
    max-width: 484px;
    padding: 0;
    margin: auto;
}
.form__body > div > span {
    color: #6e6e6e;
    font-size: 14px;
    margin-bottom: 16px;
    display: block;
}
.form__body > div > label > span {
    margin-bottom: 8px;
    color: #cccccc;
    font-size: 14px;
    height: auto;
    display: block;
}
.form__body > div > input {
    background-color: transparent;
    border: 1px solid #4d4d4d;
    border-radius: 8px;
    min-width: 450px;
    padding: 6px 14px;
    margin-bottom: 16px;
    height: 47px;
    color: #e2e2e2;
}
.form__body > div > input:focus {
    outline: 0;
    outline-offset: 0;
    background-color: #252525;
    border-color:#929292;
}
.passInput {
    display: flex;
    flex-direction: row;
    border: 1px solid #808080;
    border-radius: 8px;
    position: relative;
    display: flex;
    align-items: center;
}
.passInput input{
    color: #cccccc;
    padding: 10px 30px 10px 10px;
    border: none;
    border-radius: 8px;
    background-color: transparent;
}
.passInput input:focus{
    outline: 0;
    outline-offset: 0;
    background-color: #252525;
    border-color:#929292;
}
.USER__PASSWORD__input__svg__wrapper {
    cursor: pointer;
    position: absolute;
    right: 0;
    padding-right: 15px;
}
.USER__PASSWORD__input__svg__close svg {
    fill: #cccccc;
}
.USER__PASSWORD__input__svg__open svg {
    fill: #cccccc;
}
.confirmPass {
    padding-bottom: 24px;
}
.registrationBtn {
    display: flex;
    justify-content: center;
    border: none;
    border-radius: 10px;
    background-color: #ff5b00;
    color: #ffffff;
    width: 100%;
    cursor: pointer;
    margin-bottom: 15px;
    width: 100%;
    opacity: 1;
}
.registrationBtn:hover {
    opacity: 1;
}
.registrationBtn a {
    width: 100%;
    height: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.licenseBlock {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: center;
}
.licenseBlock__text {
    font-size: 12px;
    color: #cccccc;
    font-weight: 200;
    margin-right: 5px;
}
.licenseBlock__link {
    font-size: 12px;
    color: #ececec;
    cursor: pointer;
    font-weight: 500;
}
.licenseBlock__link:hover {
    color: #cccccc;
    opacity: 1;
}
.pass__info {
    margin-top: 5px;
}
.passError {
    color: red;
    font-size: 12px;
    margin-bottom: 5px;
    display: block;
    margin-top: 5px;
}
.confirmPassError {
    color: red ;
    font-size: 12px;
    display: block;
    margin-top: 5px;
}
.secondNameError {
    color: red ;
    font-size: 12px;
    display: block;
    margin-top: -10px;
    margin-bottom: 5px;
}
.firstNameError {
    color: red ;
    font-size: 12px;
    display: block;
    margin-top: -10px;
    margin-bottom: 5px;
}
.middleNameError {
    color: red ;
    font-size: 12px;
    display: block;
    margin-top: -10px;
    margin-bottom: 5px;
}
.eMailError {
    color: red ;
    font-size: 12px;
    display: block;
    margin-top: -10px;
}
.firstPhoneError {
    color: red ;
    font-size: 12px;
    display: block;
    margin-top: -10px;
}
.secondPhoneError {
    color: red ;
    font-size: 12px;
    display: block;
    margin-top: -10px;
}

@media screen and (max-width:600px){
    .form__body > div > input {
        min-width: 100%;
    }
    .form__registration {
        max-width: 484px;
        padding: 0 32px;
        margin: 0;
    }
    .licenseBlock {
        flex-wrap: wrap;
    }
}