

.Catalog__wrapper {
    margin: 0 auto;
    max-width: 1564px;
}

.Catalog {
    margin-top: 11.75vh;
    color: #fff;
    padding: 0 32px;
}
.title {
    font-size: 35px;
    margin-bottom: 5vh;
    font-weight: 700;
}
.btn__svg svg {
    width: 120px;
    height: 120px;
    margin: auto auto 13px;
}
.btn__wrapper {
    background-color: #232323;
    border: 1px solid #6e6e6e;
    border-radius: 12px;
    padding: 40px 20px 35px 20px;
    width: 220px;
    cursor: pointer;
}
.btn__wrapper:hover .btn__text{
    opacity: 0.65;
}
.btns {
    display: flex;
    flex-direction: row;
    gap: 30px;
}
.btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.btns__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn__text {
    display: flex;
    flex-wrap: nowrap;
}
@media screen and (max-width:800px){
    .btns {
        display: grid;
        grid-template-columns: repeat(2, auto);
    }
}
@media screen and (max-width:600px){
    .btns {
        display: grid;
        grid-template-columns: repeat(1, auto);
        width: 100%;
    }
    .btn__wrapper {
        width: 100%;
        background-color: #232323;
        border: 1px solid #6e6e6e;
        border-radius: 12px;
        padding: 12px 20px;
        cursor: pointer;
        border-radius: 8px;
    }
    .btn {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
    }
    .btn__svg {
        margin-right: 16px;
    }
    .btn__svg svg {
        width: 35px;
        height: 35px;
        margin: auto;
    }
}