.footer {
    margin-top: 50px;
}
.container {
    margin-left: auto !important;
    margin-right: auto !important;
    max-width: calc(1500px + 64px);
    position: relative;
    overflow: visible;
}
.main {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 0 32px;
}
.main__itemsAndMail {
    margin-bottom: 2rem;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: space-between;
}   
.items {
    /* width: 1025px; */
    row-gap: 20px;
    display: flex;
    flex-wrap: wrap;
    color: #fff;
}
.items a {
    cursor: pointer;
    text-decoration: none;
    color: #fff;
}
.items a:hover {
    opacity: 0.6;
}
.items a:not(:last-of-type) {
    margin-right: 2.55319vw;
}
.mail a {
    text-decoration: none;
    color: #cccccc;
}
.main__interaction {
    display: flex;
    flex-direction: row;
    margin-bottom: 5rem;
    align-items: center;
}
.main__interaction_form {
    display: flex;
    flex-direction: row;
    margin-right: 5.5rem;
}
.main__interaction_form button {
    display: flex;
    justify-content: center;
    align-items: center;
}
.form {
    background: transparent;
    border: 1px solid rgb(255, 255, 255, 0.4);
    border-radius: 0.5rem;
    width: 15rem;
    height: 3.125rem;
    font-size: 1rem;
    margin-right: 0.75rem;
}
.form input {
    background: transparent;
    border: none;
    padding: 1rem 0 1rem 1rem;
    color: #fff;
    width: 100%;
    outline: none;
    font-size: 1rem;
    height: 100%;
}
.form input::placeholder {
    color: #fff;
}
.main__interaction_form button {
    background: rgb(44, 44, 44);
    border: none;
    border-radius: 0.5rem;
    width: 4.25rem;
    height: 3.125rem;
    cursor: pointer;
}
.main__main__line {
    border-bottom: 1px solid rgba(158, 158, 158, 0.4);
}
.bottom {
    margin-top: 2.5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 2.7rem;
    padding: 0 32px;
}
.bottom__copyright {
    color: #cccccc;
}
.bottom__pays {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 20px;
}
.bottom__items {
    display: flex;
    flex-direction: row;
    color: #cccccc;
    cursor: pointer;
}
.bottom__items a {
    display: flex;
    flex-direction: row;
    color: #cccccc;
    cursor: pointer;
    text-decoration: none;
}
.bottom__items a:not(:last-of-type) {
    margin-right: 1vw;
}
.bottom__items a:hover {
    opacity: 0.75;
}
.items span:hover {
    opacity: 0.75;
}
.bottom__items span:not(:last-of-type) {
    margin-right: 1.7rem;
}
.btns {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    gap: 15px;
}
.btns button {
    padding: 9px 9px 9px 9px;
    background: rgb(54 54 54);
    border: none;
    border-radius: 0.53rem;
    cursor: pointer;
    color: #5fb7ff;
}
.btns button svg{
    width: 28px;
    height: 28px;
}
.scrollToTop {
    cursor: pointer;
    height: 2.5rem;
    width: 2.5rem;
    background-color: rgb(215 22 88);
    border-radius: 50%;
    z-index: 15;
    justify-content: center;
    align-items: center;
    left: 76.735%;
    position: sticky;
    display: flex;
    top: 0;
    bottom: 0;
}
.scrollToTop svg {
    transform:rotate(270deg)
}
@media screen and (max-width:1000px){
    .main__interaction {
        display: flex;
        flex-direction: column;
        margin-bottom: 5rem;
        align-items: flex-start;
        gap: 20px;
    }
    .main__interaction_form {
        display: flex;
        flex-direction: row;
        margin-right: 0;
        width: 100%;
    }
    .main__interaction_form button{
        min-width: 68px;
    }
    .main__interaction_form button {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .form {
        width: 100%;
        min-width: 90%;
        margin: 0 12px 0 0;
    }
    .main__itemsAndMail {
        flex-wrap: wrap;
        justify-content: flex-start;
    }   
    .items a:last-of-type {
        margin-right: 20px;
        margin-bottom: 8px;
    }
}
@media screen and (max-width:800px) {
    .bottom {
       flex-wrap: wrap;
    }
    .bottom__items a:not(:last-of-type) {
        margin-right: 20px;
    }
    .bottom__copyright {
        margin-right: 10px;
    }
    .bottom__pays {
        margin-right: 10px;
    }
}

@media screen and (max-width:600px) {
    .main__interaction_form {
        display: flex;
        flex-direction: column;
        margin-right: 0;
        width: 100%;
        gap: 20px;
    }
    .main__interaction_form button{
        width: 100%;
    }
    .form {
        width: 100%;
        min-width: 100%;
        margin: 0;
    }
} 